
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'

export default {
name: 'SpecialStatInfo',
components: {
Group,
Cell
},
props: {
specialInfo: {
type: Array,
require: true
},
prefix: {
type: String,
default: '本月'
}
},
computed: {
/**
* 组装特殊信息
* @return {String} 组装后的信息
*/
joinMsg: function() {
let strHtml = [];
for(let info of this.specialInfo) {
strHtml.push(`${info.state}<span style="color: ${info.color};">${info.number}</span>${info.unit}`)
}
return this.prefix+strHtml.join(',');
}
}
}



