
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'

import StatItem from '@/components/attendance/StatItem'
export default {
name: 'StatList',
components: {
StatItem,
Flexbox,
FlexboxItem
},
props: {
statList: {
type: Array,
require: true
},
span: {
type: Number
}
},
computed: {
/**
* 判断statList长度，以便补充到3个item
* @return {Number} 0或3-statList长度
*/
getLeftLen: function() {
return 3-this.statList.length>0?3-this.statList.length:0
}
}
}



