
import XButton from 'vux/src/components/x-button/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'

import StatList from '@/components/attendance/StatList'
import StateList from '@/components/attendance/StateList'
import RiliTable from '@/components/attendance/RiliTable'
import SpecialStatInfo from '@/components/attendance/SpecialStatInfo'
import LeaveList from '@/components/attendance/LeaveList'
import { format } from '@/utils/format'
import LingLoading from '@/components/attendance/LingLoading'
export default {
name: 'TeacherAttendanceHome',
components: {
StatList,
XButton,
Flexbox,
FlexboxItem,
Group,
Cell,
StateList,
RiliTable,
SpecialStatInfo,
LeaveList,
LingLoading
},
data() {
return  {
statList: [
{ label: "总人数", number: 99 },
{ label: "实到", number: 99 },
{ label: "未到", number: 99 }
],
stateList: [
{state: "请假", color: "blue"},
{state: "生病", color: "red"},
{state: "发热", color: 'gray'}
],
leaveList: [,,,,,],
time: new Date('2016-07-28'),
specificDates: [{time: new Date(), type: 2}, {time: new Date(), type: 1}, {time: new Date('2017-7-29'), type: 2}],
specificColors: {1: 'blue', 2: 'gray'},
specialInfo: [
{state: "请假", color: "red", number: 3, unit: '次'},
{state: "生病", color: "blue", number: 2, unit: '次'},
{state: "发热", color: "green", number: 1, unit: '次'}
]
}
},
methods: {
handleDetail: function(leave) {
this.$router.push('/teacher/attendance/leaveDetail')
},
handleLeave: function() {
this.$router.push('/teacher/attendance/teacherLeave')
},
fetchData: function () {

this.$store.dispatch('actionTeacherAttendance', {date: '2016-07-28', staffId: 23})
}
},
computed: {
/**
* 组装某老师的班级出勤统计信息
* @return {Array} 班级出勤统计信息
*/
getStatList: function() {
let info = this.$store.getters.getTeacherAttendance
return [
{ label: "总人数", number: info.allNum },
{ label: "实到", number: info.arriveNum },
{ label: "未到", number: info.noArriveNum }
]
},
/**
* 组装这月该老师的出勤信息列表
* @return {Array} 出勤信息列表
*/
getSpecificDates: function() {
let arr = [], list = this.$store.getters.getTeacherAttendance.teacherAttendance
for (let l in list) {
switch(list[l].sign_status) {
case 2:
case 4:
break
case 3:
case 6:
arr.push({
time: list[l].sign_date,
type: 1
})
break
case 5:
arr.push({
time: list[l].sign_date,
type: 2
})
break
case 1:
arr.push({
time: list[l].sign_date,
type: 3
})
break
case 7:
arr.push({
time: list[l].sign_date,
type: 1
})
arr.push({
time: list[l].sign_date,
type: 2
})
break
default:
console.log('not know type')
}
}
return arr
},
/**
* 获取老师的考勤类型
* @returns  {Array} 出勤类型列表
*/
getStateList: function () {
return this.$store.getters.getTeacherAttendanceType
},
/**
* 获取老师出勤状态对应的颜色
* @returns {Object}  出勤状态对应的颜色
*/
getSpecificColors: function () {
return this.$store.getters.getTeacherAttendanceColor
},
/**
* 获取日历表头显示的时间
* @returns {String}  按照YYYY-MM格式化的时间
*/
getViewTime: function () {
return format(new Date(), 'YYYY-MM')
},
/**
* 获取，取数据时的状态
* @returns {Object}
*/
getLoadingState: function () {
return this.$store.getters.getLingLoading
},
/**
* 获取选择的时间
* @returns {String}
*/
getSelectTime: function() {
return this.$parent.$data.selectTime
}
},
watch: {
/**
* 监听状态变化，当出现错误状态时，弹出提示
* @param val
*/
getLoadingState: function(val) {
if(val.state === 'error') {
this.$vux.toast.show({
text: val.message,
type: 'text'
})
}
},
/**
* 监听选择时间的变化
* @param val
*/
getSelectTime: function(val) {
window.console.log(val)
}
},
created () {
this.fetchData()
}
}



